import React from 'react';
import { Link } from 'react-router-dom';


const NavBar = () => {
    return(
    <nav>
        <ul>
            <li>
                <Link to="/">Home</Link>
            </li>
            <li>
                <Link to="/articlespage">Articles</Link>
            </li>
            <li>
                <Link to="/aboutpage">About</Link>
            </li>
        </ul>
    </nav>
    )
}

export default NavBar;