import React from 'react';
import { Link } from 'react-router-dom';


const List = ({ articles, number }) => {
    return(
    <>
    <ul>
        {articles.map((paragraph, key) => (
                <Link key={key} className="article-list-item" to={`/articlepage/${paragraph.name}`}>
                    <h3>{paragraph.title}</h3>
                    <p>{paragraph.content[0].substring(0, number)}</p>                   
                    </Link>
        ))}
    </ul>
    </>
    )
}

export default List;