import React, { useState, useEffect } from 'react';
import articles from './article-content';
import List from '../components/list';
import NotFound from './NotFound';
import CommentsList from '../components/CommentsList';
import Upvotes from "../components/Upvotes";
import AddComments from '../components/AddComents';

// Cuando usamos react route y dentro de la url hay un parametro ":name", se inserta un parametro "match" que contiene los parametros de la utl.
const ArticlePage = ({ match }) => {
    const name = match.params.name;
    const article = articles.find ( art => art.name === name);

    const [articleInfo, setArticleInfo] = useState({ upvotes: 0, comments: []});
    
    useEffect(()=>{
        const fetchData = async () => {
            const result = await fetch(`/api/articles/${name}`);
            const body = await result.json();
            setArticleInfo(body)
        }
        fetchData();
    }, [name])

    if (!article) {return(<NotFound/>)}
    
    const otherarticles = articles.filter ( art => art.name != name)
    
    return(
    <>
    <h1>{article.title}</h1>
    <Upvotes articleName={name} upvotes={articleInfo.upvotes} setArticleInfo={setArticleInfo}/>
    {article.content.map((paragraph, key) => (
        <p key={key}>{paragraph}</p>
    ))}
    <br></br>
    <CommentsList comment={articleInfo.comments}/>
    <AddComments articleName={name} setArticleInfo={setArticleInfo}/>
    <br></br>
    <h2>Other Articles:</h2>
    <List articles={otherarticles} number={150}/>
    </>
    )
}

export default ArticlePage;